.banner {
    width: 100%;
    position: relative;
    img {
        width: 100%;
        height: auto;
    }
}

.suggest {
    padding-top: 100px;
    padding-bottom: 100px;
    h2 {
        font-size: 50px;
        color: #ee583f;
        font-weight: normal;
        text-align: center;
        margin-bottom: 10px;
    }
    .en {
        color: #ee583f;
        font-size: 30px;
        text-align: center;
        margin-bottom: 50px;
    }
    p {
        font-size: 22px;
        text-align: center;
        line-height: 40px;
    }
    .wishper {
        text-align: right;
        color: #989898;
        border-bottom: 2px dashed #ccc;
        margin-top: 100px;
    }
    .flow {
        width: 100%;
        .m-left {
            float: left;
            width: 550px;
            margin-left: 160px;
            h2 {
                margin-top: 90px;
                text-align: left;
                font-size: 45px;
            }
            p {
                text-align: left;
                margin-top: 30px;
                font-size: 40px;
            }
            img {
                margin-top: 55px;
            }
        }
        .m-right {
            margin-top: 111px;
            margin-right: 150px;
            border-left: 2px solid #ee583f;
            float: right;
            height: 415px;
            li {
                margin-left: -16px;
                width: 330px;
                height: 100px;
                // background-color: #ccc;
                .item {
                    width: 100%;
                    height: 33px;
                    div {
                        float: left;
                        width: 30px;
                        height: 30px;
                        background-color: #ee583f;
                        line-height: 28px;
                        text-align: center;
                        border-radius: 50%;
                        color: #fff;
                        font-size: 20px;
                    }
                    span {
                        float: left;
                        font-size: 24px;
                        padding-left: 10px;
                        line-height: 30px;
                    }
                }
                p {
                    font-size: 20px;
                    text-align: left;
                    margin-left: 40px;
                    line-height: 30px;
                }
            }
        }
    }
}

// <!-- 企业入驻 -->
.bill {
    width: 100%;
    height: 700px;
    background: url(../images/seller-add/57872ef89c7d4.jpg);
    background-repeat: no-repeat;
    background-attachment: fixed;
    // padding-top: 100px;
    background-position: center center;
    background-size: cover;
    .bill-box {
        padding-top: 12px;
        padding-bottom: 50px;
        .pic {
            float: left;
            margin-left: 70px;
            margin-top: 30px;
        }
        .text {
            float: right;
            margin-top: 150px;
            width: 550px;
            color: #fff;
            margin-right: 150px;
            h2 {
                font-weight: normal;
                font-size: 50px;
                text-align: right;
                line-height: 60px;
            }
            p {
                margin-top: 50px;
                font-size: 24px;
                text-align: right;
                line-height: 42px;
            }
        }
    }
}

// <!-- 个人入驻 -->
.user-add {
    height: 670px;
    padding-top: 30px;
    .pic {
        float: right;
        margin-top: 30px;
        margin-right: 200px;
    }
    .text {
        margin-left: 160px;
        float: left;
        margin-top: 150px;
        width: 550px;
        h2 {
            font-weight: normal;
            font-size: 50px;
            line-height: 60px;
        }
        .entit {
            color: #ee583f;
        }
        p {
            margin-top: 50px;
            font-size: 24px;
            line-height: 42px;
        }
    }
}

.bottom-pic {
    width: 100%;
    img {
        width: 100%;
        height: auto;
    }
}

.footer {
    margin-top: 0;
}